#depressionStation {
  text-align: center;
  background: url("images/bg.jpg") no-repeat center top #000;
}

h1 { 
  font-weight: bold;
  color: #fff;
}

.detail-view, .summary-view{
  margin-top: 50px;
}

.infoRow{
  margin-top: 20px;
}

.infoButton,
.infoTitle,
.infoData{
  display:inline-block;
  margin-right:20px;
}

.infoTitle{
  width:300px;
  font-weight: bold;
  text-align: left;
  color: #fff;
  font-size: 16px;
}
.infoData input{
  width:100px;
}

.infoButton button{
  -webkit-appearance: button;
  background-color: #167F92;
  color: #fff;
  border: 0;
  font-weight: bold;
  font-size: 16px;
  padding: 10px;
}

input[type=number]::-webkit-inner-spin-button, 
input[type=number]::-webkit-outer-spin-button {  
   opacity: 1;
}

table th{
    border: 2px solid #FFF;
    background-image: -webkit-linear-gradient(top,#255087 0,#1B2C60 100%);
    background-image: -o-linear-gradient(top,#255087 0,#1B2C60 100%);
    background-image: -webkit-gradient(linear,left top,left bottom,from(#255087),to(#1B2C60));
    background-image: -webkit-gradient(linear,left top, left bottom,color-stop(0, #255087),to(#1B2C60));
    background-image: linear-gradient(to bottom,#255087 0,#1B2C60 100%);
    filter: progid:DXImageTransform.Microsoft.gradient(startColorstr='#ff255087', endColorstr='#ff1B2C60', GradientType=0);
    filter: progid:DXImageTransform.Microsoft.gradient(enabled=false);
    background-repeat: repeat-x;
    border-color: #245580;
    color: #FFF;
    font-size: 16px;
}

table .td-even{
  background-color: #fff;
}

table .td-odd{
  background-color: #d4dff1;
}


@-webkit-keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}


@keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}



